.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  min-height: 180px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-subtitle {
  font-size: 1em;
  padding-top: 10px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.textbox__input,
.textbox__output {
  height: 300px;
  max-height: 600px;
  overflow: auto !important;
}

.textbox__input {
  box-shadow: 0 0 8px #003bff !important;
}

.textbox__output {
  background: #e5e5e5 !important;
  box-shadow: 0 0 8px #883800 !important;
}

.vert-divider__8 {
  height: 8px;
}

.prettyprint {
  text-align: left;
}

.audit-container {
  border-radius: 5px 20px;
  background: #e5e5e5;
  padding: 20px;
  margin: 50px 10px 0 0;
}

.audit-check {
  min-height: 32px;
  border-radius: 5px 20px;
}
.audit-check.passed {
  background: #bbffc5;
}
.audit-check.failed {
  background: #eba3ba;
}